:root {
    --color-3: #F71B3D;
}

html {
    background: #222528;
    color: #B3B5C2;
    font-family: 'gaultier', 'SongTi TC', serif;
    font-variant-numeric: lining-nums;
}

footer {
    color: #B3B5C2;
}

main {
    font-size: 1em;
}

header {
    margin-top: 1em;
    padding-top: 7em;
    margin-right: 1em;
    background:
